/*-- -------------------------- -->
<---     Mobile Navigation      -->
<--- -------------------------- -*/
/* Mobile - 1023px */
@media only screen and (max-width: 63.9375rem) {

  .cs-disability-icon {
    z-index: 1;
  }

  .cs-navigation .cs-li-link.cs-active-link {
    color: var(--secondary);
  }

  .cs-open {
    overflow: hidden;
  }

  .cs-navigation.scroll {
    width: 100%;
    max-width: 100%;
    top: 0;
  }

  .cs-navigation.scroll:before {
    border-radius: 0;
  }

  .cs-navigation.scroll .cs-ul-wrapper {
    top: 100%;
  }

  .cs-navigation {
    width: 94%;
    max-width: 80rem;
    /* prevents padding from affecting height and width */
    box-sizing: border-box;
    /* 12px - 24px */
    padding: clamp(0.75rem, 2vw, 1.5rem);
    /* 12px - 24px */
    border-radius: clamp(0.75rem, 2vw, 1.5rem);
    position: fixed;
    top: 2rem;
    left: 50%;
    z-index: 10000;
    transform: translateX(-50%);
    transition: top 0.3s, border-radius 0.3s, width 0.3s, max-width 0.3s;
  }

  .cs-navigation:before {
    /* background color */
    content: "";
    width: 100%;
    height: 100%;
    background: #fff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    opacity: 1;
    /* 12px - 24px */
    border-radius: clamp(0.75rem, 2vw, 1.5rem);
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: transform 0.2s, border-radius 0.3s ease-in-out;
  }

  .cs-navigation .cs-active .cs-toggle {
    transform: rotate(180deg);
  }

  .cs-navigation .cs-active .cs-ul-wrapper {
    width: 60%;
    top: 85%;
    overflow: auto;
    transform: scaleY(1);
  }

  .cs-navigation .cs-active .cs-li {
    opacity: 1;
    overflow: visible;
    transform: translateX(0);
  }

  .cs-navigation .cs-container {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1.5rem;
  }

  .cs-navigation .cs-logo {
    width: auto;
    max-width: 12.5rem;
    height: 100%;
    margin: 0 auto 0 0;
    /* prevents padding from affecting height and width */
    box-sizing: border-box;
    padding: 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    z-index: 10;
  }

  .cs-navigation .cs-logo img {
    width: 100%;
    height: 52px;
    /* ensures the image never overflows the container. It stays contained within it's width and height and expands to fill it then stops once it reaches an edge */
    object-fit: contain;
  }

  .cs-navigation .cs-toggle {
    width: 3.5rem;
    height: 3.5rem;
    margin: 0 0 0 auto;
    background-color: #1a1a1a;
    border: none;
    border-radius: 0.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 10;
    transition: transform 0.6s;
  }

  .cs-navigation .cs-nav {
    /* sends it to the right in the 3rd position */
    order: 3;
  }

  .cs-navigation .cs-contact-group {
    display: none;
    position: relative;
    z-index: 1;
  }

  .cs-navigation .cs-phone {
    font-size: 1rem;
    line-height: 1.5em;
    text-decoration: none;
    margin: 0;
    color: var(--headerColor);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5rem;
    transition: opacity 0.3s, color 0.3s;
  }

  .cs-navigation .cs-phone-icon {
    width: 1.5rem;
    height: auto;
    display: block;
  }

  .cs-navigation .cs-social {
    display: none;
  }

  .cs-navigation .cs-active .cs-line1 {
    top: 50%;
    transform: translate(-50%, -50%) rotate(225deg);
  }

  .cs-navigation .cs-active .cs-line2 {
    top: 50%;
    transform: translate(-50%, -50%) translateY(0) rotate(-225deg);
    transform-origin: center;
  }

  .cs-navigation .cs-active .cs-line3 {
    opacity: 0;
    bottom: 100%;
  }

  .cs-navigation .cs-box {
    /* 24px - 28px */
    width: clamp(1.5rem, 2vw, 1.75rem);
    height: 1rem;
    position: relative;
  }

  .cs-navigation .cs-line {
    width: 100%;
    height: 2px;
    background-color: #fafbfc;
    border-radius: 2px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  .cs-navigation .cs-line1 {
    top: 0;
    transition: transform 0.5s, top 0.3s, left 0.3s;
    animation-duration: 0.7s;
    animation-timing-function: ease;
    animation-direction: normal;
    animation-fill-mode: forwards;
    transform-origin: center;
  }

  .cs-navigation .cs-line2 {
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    transition: top 0.3s, left 0.3s, transform 0.5s;
    animation-duration: 0.7s;
    animation-timing-function: ease;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }

  .cs-navigation .cs-line3 {
    bottom: 0;
    transition: bottom 0.3s, opacity 0.3s;
  }

  .cs-navigation .cs-ul-wrapper {
    width: 20%;
    height: auto;
    max-height: 70vh;
    background-color: #fff;
    border-radius: 0 0 1.5rem 1.5rem;
    position: absolute;
    top: 20%;
    right: 0;
    left: auto;
    z-index: -1;
    overflow: hidden;
    transform: scaleY(0);
    transition: transform 0.3s, width 0.5s, top 0.3s;
    transition-delay: width 0.3s;
    transform-origin: top right;
  }

  .cs-navigation .cs-ul {
    width: auto;
    min-width: 40%;
    height: auto;
    margin: 0;
    /* 32px - 40px */
    padding: 4rem clamp(2rem, 3vw, 2.5rem) 4rem 2rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    gap: 1.25rem;
    overflow: hidden;
  }

  .cs-navigation .cs-li {
    text-align: right;
    list-style: none;
    width: 100%;
    margin-right: 0;
    opacity: 0;
    /* transition from these values */
    transform: translateX(-2.5rem);
    transition: transform 0.6s, opacity 0.9s;
  }

  .cs-navigation .cs-li:nth-of-type(1) {
    transition-delay: 0.05s;
  }

  .cs-navigation .cs-li:nth-of-type(2) {
    transition-delay: 0.1s;
  }

  .cs-navigation .cs-li:nth-of-type(3) {
    transition-delay: 0.15s;
  }

  .cs-navigation .cs-li:nth-of-type(4) {
    transition-delay: 0.2s;
  }

  .cs-navigation .cs-li:nth-of-type(5) {
    transition-delay: 0.25s;
  }

  .cs-navigation .cs-li:nth-of-type(6) {
    transition-delay: 0.3s;
  }

  .cs-navigation .cs-li:nth-of-type(7) {
    transition-delay: 0.35s;
  }

  .cs-navigation .cs-li:nth-of-type(8) {
    transition-delay: 0.4s;
  }

  .cs-navigation .cs-li:nth-of-type(9) {
    transition-delay: 0.45s;
  }

  .cs-navigation .cs-li-link {
    font-size: 1.25rem;
    line-height: 1.2em;
    text-decoration: none;
    margin: 0;
    color: var(--headerColor);
    display: inline-block;
    position: relative;
  }

  .cs-navigation .cs-li-link:before {
    /* active state underline */
    content: "";
    width: 100%;
    height: 1px;
    background: currentColor;
    opacity: 1;
    display: none;
    position: absolute;
    bottom: -0.125rem;
    left: 0;
  }

  .cs-navigation .cs-li-link.cs-active:before {
    display: block;
  }

  .cs-navigation .cs-button-solid {
    display: none;
  }
}

/* Tablet - 768px */
@media only screen and (min-width: 48rem) {
  .cs-navigation .cs-contact-group {
    display: block;
  }
}

/*-- -------------------------- -->
  <---     Navigation Dropdown    -->
  <--- -------------------------- -*/
/* Mobile - 1023px */
@media only screen and (max-width: 63.9375rem) {
  .cs-navigation .cs-dropdown {
    color: var(--bodyTextColorWhite);
    position: relative;
  }

  .cs-navigation .cs-dropdown.cs-active .cs-drop-ul {
    height: auto;
    margin: 0.75rem 0 0 auto;
    padding: 1.5rem;
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }

  .cs-navigation .cs-dropdown.cs-active .cs-drop-link {
    opacity: 1;
  }

  .cs-navigation .cs-dropdown .cs-li-link {
    position: relative;
    transition: opacity 0.3s;
  }

  .cs-navigation .cs-drop-icon {
    width: 0.9375rem;
    height: auto;
    position: absolute;
    top: 50%;
    right: -1.25rem;
    transform: translateY(-50%);
  }

  .cs-navigation .cs-drop-ul {
    width: 100%;
    max-width: 15.625rem;
    height: 0;
    margin: 0 0 0 auto;
    box-sizing: border-box;
    padding: 0 1.5rem 0 1.5rem;
    opacity: 0;
    border-radius: 1rem;
    display: flex;
    visibility: hidden;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    gap: 0.75rem;
    overflow: hidden;
    transform: scale(0);
    transition: padding 0.3s, margin 0.3s, height 0.3s, opacity 0.3s, transform 0.3s, visibility 0.3s;
    transform-origin: top right;
  }

  .cs-navigation .cs-drop-li {
    text-align: inherit;
    list-style: none;
  }

  .cs-navigation .cs-li-link.cs-drop-link {
    /* 14px - 16px */
    font-size: clamp(0.875rem, 2vw, 1.25rem);
  }
}

/* Desktop - 1024px */
@media only screen and (min-width: 64rem) {
  .cs-navigation .cs-li-link.cs-drop-link.cs-active-link {
    color: var(--secondary);
  }

  .cs-navigation .cs-li-link.cs-active-link {
    color: var(--secondary);
  }

  .cs-navigation .cs-dropdown {
    position: relative;
  }

  .cs-navigation .cs-dropdown:hover {
    cursor: pointer;
  }

  .cs-navigation .cs-dropdown:hover .cs-drop-ul {
    opacity: 1;
    visibility: visible;
    transform: scaleY(1);
  }

  .cs-navigation .cs-dropdown:hover .cs-drop-li {
    opacity: 1;
    transform: translateY(0);
  }

  .cs-navigation .cs-drop-icon {
    width: 0.9375rem;
    height: auto;
    display: inline-block;
  }

  .cs-navigation .cs-drop-ul {
    min-width: 12.5rem;
    margin: 0;
    padding: 0;
    background-color: #fff;
    box-shadow: inset rgba(149, 157, 165, 0.1) 0px 8px 10px;
    opacity: 0;
    border-bottom: 5px solid var(--secondary);
    border-radius: 0 0 1.5rem 1.5rem;
    visibility: hidden;
    /* if you have 8 or more links in your dropdown nav, uncomment the columns property to make the list into 2 even columns. Change it to 3 or 4 if you need extra columns. Then remove the transition delays on the cs-drop-li so they don't have weird scattered animations */
    position: absolute;
    top: 100%;
    z-index: -100;
    overflow: hidden;
    transform: scaleY(0);
    transition: transform 0.3s, visibility 0.3s, opacity 0.3s;
    transform-origin: top;
  }

  .cs-navigation .cs-drop-li {
    font-size: 1rem;
    text-decoration: none;
    list-style: none;
    width: 100%;
    height: auto;
    opacity: 0;
    display: block;
    transform: translateY(-0.625rem);
    transition: opacity 0.6s, transform 0.6s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(1) {
    transition-delay: 0.05s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(2) {
    transition-delay: 0.1s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(3) {
    transition-delay: 0.15s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(4) {
    transition-delay: 0.2s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(5) {
    transition-delay: 0.25s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(6) {
    transition-delay: 0.3s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(7) {
    transition-delay: 0.35s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(8) {
    transition-delay: 0.4s;
  }

  .cs-navigation .cs-drop-li:nth-of-type(9) {
    transition-delay: 0.45s;
  }

  .cs-navigation .cs-li-link.cs-drop-link {
    font-size: var(--bodyFontSize);
    line-height: 1.5em;
    text-transform: capitalize;
    text-decoration: none;
    white-space: nowrap;
    width: 100%;
    /* prevents padding from affecting height and width */
    box-sizing: border-box;
    padding: 0.75rem;
    color: var(--headerColor);
    display: block;
    transition: color 0.3s, background-color 0.3s;
  }

  .cs-navigation .cs-li-link.cs-drop-link:hover {
    color: var(--bodyTextColorWhite);
    background-color: var(--secondary);
  }

  .cs-navigation .cs-li-link.cs-drop-link:before {
    display: none;
  }
}

/*-- -------------------------- -->
  <---     Desktop Navigation     -->
  <--- -------------------------- -*/
/* Small Desktop - 1024px */
@media only screen and (min-width: 64rem) {
  .scroll.cs-navigation {
    width: 100%;
    max-width: 100%;
    border-radius: 0;
    top: 0;
  }

  .cs-navigation {
    width: 94%;
    max-width: 90rem;
    /* prevents padding from affecting height and width */
    box-sizing: border-box;
    /* 12px - 24px */
    padding: clamp(0.75rem, 2vw, 1.5rem) 0;
    background-color: #fff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    /* 12px - 24px */
    border-radius: clamp(0.75rem, 2vw, 1.5rem);
    align-items: center;
    position: fixed;
    top: 2rem;
    left: 50%;
    z-index: 10000;
    transform: translateX(-50%);
    transition: top 0.3s, border-radius 0.3s, width 0.3s, max-width 0.3s;
  }

  .cs-navigation .cs-container {
    width: 100%;
    max-width: 90rem;
    margin: auto;
    /* prevents padding from affecting height and width */
    box-sizing: border-box;
    padding: 0 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
  }

  .cs-navigation .cs-toggle {
    display: none;
  }

  .cs-navigation .cs-logo {
    width: 18.4%;
    max-width: 21.875rem;
    height: 2rem;
    /* margin-right auto pushes everything away from it to the right */
    margin: 0 auto 0 0;
    padding: 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    z-index: 100;
  }

  .cs-navigation .cs-logo img {
    width: auto;
    height: 52px;
    /* ensures the image never overflows the container. It stays contained within it's width and height and expands to fill it then stops once it reaches an edge */
    object-fit: contain;
  }

  .cs-navigation .cs-contact-group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
  }

  .cs-navigation .cs-phone {
    font-size: 1rem;
    line-height: 1.5em;
    text-decoration: none;
    margin: 0;
    color: var(--headerColor);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5rem;
    transition: opacity 0.3s, color 0.3s;
  }

  .cs-navigation .cs-phone-icon {
    width: 1.5rem;
    height: auto;
    display: block;
  }

  .cs-navigation .cs-social {
    height: 2rem;
    opacity: 1;
    display: none;
    visibility: visible;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    transition: opacity 0.3s, visibility 0.3s, height 0.3s;
  }

  .cs-navigation .cs-social-link {
    text-decoration: none;
    width: 2rem;
    height: 2rem;
    background-color: #f7f7f7;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s;
  }

  .cs-navigation .cs-social-link:hover {
    background-color: var(--secondary);
  }

  .cs-navigation .cs-social-link:hover .cs-social-icon {
    opacity: 1;
    filter: grayscale(1) brightness(10000%);
  }

  .cs-navigation .cs-social-icon {
    width: 0.75rem;
    height: auto;
    opacity: 0.6;
    display: block;
    transition: opacity 0.3s;
  }

  .cs-navigation .cs-ul-wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    /* absolutely positioned to be dead center */
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .cs-navigation .cs-ul {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    /* 20px - 36px */
    gap: clamp(1.25rem, 2.6vw, 2.25rem);
  }

  .cs-navigation .cs-li {
    list-style: none;
    height: 100%;
    padding: 0;
    display: flex;
    align-items: center;
    /* prevent flexbox from squishing it */
    flex: none;
  }

  .cs-navigation .cs-li-link {
    font-size: var(--bodyFontSize);
    line-height: 1.5em;
    text-decoration: none;
    margin: 0;
    color: var(--headerColor);
    display: block;
    position: relative;
    transition: color 0.3s;
  }

  .cs-navigation .cs-li-link:hover {
    color: var(--secondary);
  }

}

/* Large Desktop - 1300px */
@media only screen and (min-width: 81.25rem) {
  .cs-navigation .cs-social {
    display: flex;
  }
}



/*AAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAA*/


.cs-navigation .cs-container.cs-container_window {
  margin-top: 5px;
  justify-content: center;
}


.cs-window {
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  gap: 15px;
  overflow: hidden;
}



.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  top: 3px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 20px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked+.slider {
  background-color: var(--secondaryLight);
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 20px;
}

.slider.round:before {
  border-radius: 50%;
}


.textToggleButton {
  width: 20px;
  height: 20px;
  background-color: #ccc;
  border: none;
  border-radius: 5px;
  margin-right: 5px;
  cursor: pointer;
  padding: 0;
}

.textToggleButton:focus {
  outline: none;
}

.textToggleButton:hover {
  background-color: #ddd;
}

.textb {
  margin-bottom: 4px;
}